

    .InformationMyWork {
        display: inline-grid;
      }
            
         
          .InformationMyWork {
            width: 350px;
            height: 250px;
            aspect-ratio: 1;
            grid-gap: 10px;
            box-shadow: -2px 0px 2px 5px rgba(255,255,255,0.19);
            margin: 20px;
            }
            .InformationMyWork *{
              font-size: 30px;
              font-family: "Lucida Handwriting", cursive;
            }

      .InformationMyWorkText{
          color: white;
      }
      
      p {
        text-align: left;
        margin-left: 10px;
        margin-bottom: 0;
      }
      
      .InformationMyWorkPCode{
        margin-top: 0;
        font-size: 25px;
      }
      
      .InformationMyWorkLinkCode{
        text-align: left;
        margin-left: 10px;
        text-decoration: none;
        font-size: 20px;
        font-family:Arial, Helvetica, sans-serif;
        color: #E4C17C;
        width: 50%;
        height: 50%;
      }
      
      
